body {
  background-color: #2b2929;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.search-form {
  text-align: center;
  margin-bottom: 20px;
}

.gallery {
  background-color: #454443;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  display: grid;
}

.photo-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
}

.photo-card img {
  width: 100%;
  height: auto;
  display: block;
}

.info {
  background-color: #e8ebea;
  padding: 10px;
}

.info-item {
  margin: 0;
}

